.tiny{
  background-color: #11ce7e;
  color: #fff;
}
body{
  background-color: black;
  color: #fff;
}
.card{
  background-color: rgba(34, 31, 31, 0.493);
  color: #fff;
  border-radius: 7px;
}
.title{
  color: #cbb35f;
}
.is-4{
  margin-bottom: 4px !important;
}

.accordion {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
}

.active, .accordion:hover {
  background-color: rgb(0, 0, 0);
}

.accordion:after {
  content: '\002B';
  color: #777;
  font-weight: bolder;
  float: right;
  margin-left: 5px;
}

.active:after {
  content: "\2212";
}

.panel {
  padding: 0 18px;
  background-color: rgb(0, 0, 0);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

.tag{
  background-color: transparent !important;
  color: #cbb35f !important;

}
section, footer{
  margin-top: 50px !important;
}
.social{
  font-size: 30px;
  margin: 0 10px;
  color: #fff;
}
.cap{
  text-transform: uppercase;
}